<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process242"></a>

<p><br></p>
<p><span class="rvts17">熟悉的脸孔（</span><span class="rvts50">A Familiar Face</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:150,Y:899 </span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：魔导师德罗若斯（</span><span class="rvts20">Delorus</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">成功护送德罗若斯到精灵营地的亚历山大身边</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1355.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在游戏第一章欢乐堡部分，如果在监狱救了那个被欺负的魔导师德罗若斯（</span><span class="rvts20">Delorus</span><span class="rvts21">），他会在无名岛被淹没的神庙附近出现。</span>
<br><span class="rvts21">与他交谈，他想要找到魔导师的大部队（其实就是在精灵营地中的亚历山大），护送他到亚历山大那里，任务完成，奖励一把史诗弓。</span>
<br><span class="rvts21">注意，如果把他护送亚历山大那里后，遭遇加雷斯与亚历山大的对峙，如果我们不能成功劝服加雷斯回到复仇女神号，那么将引起战斗。如果选择帮助加雷斯打败亚历山大，那么德罗若斯会变为敌对状态。</span>
<br><span class="rvts21">德罗若斯是一个相当脆皮的</span><span class="rvts20">NPC</span><span class="rvts21">，记着在战斗中保护他。如果他一旦死亡，即便复活他也会导致该支线任务以</span><span class="rvts20"></span><span class="rvts21">德罗若斯已经死了</span><span class="rvts20"></span><span class="rvts21">的结局关闭。另外值得注意的是，精灵营地门口会有嚎叫者，使用源力汲取技能或在岛上的一些魔导师身上（或黑环士兵身上）也可以找到净化权杖，都可以破解它们。</span>
<br><span class="rvts21">另外，德罗若斯奖励的那把史诗弓，会随着我们队伍角色等级的不同而略有差异，等级越高，则奖励的弓的属性越高。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



